import types from  '../types.js';
import axios from 'axios'

var state = {
    seller:{},
    detailShow:false,
}
var getters = {
    seller( state){
        return state.seller;
    },
    detailShow(state){
       return state.detailShow;
    }
}
var actions = {
    // 
    getSeller( {commit,state} ){
        axios.get('./data.json').then((res)=>{
            // console.log(res.data)
            // if(res.data.errno === 0){
                        //   
                commit(types.GET_SELLERS,res.data.seller);
            // }
        });
    },
    showDetail({commit}){
        commit(types.showDetail);
    },
    hideDetail({commit}){
        commit(types.hideDetail); 
    }
}
var mutations = {
    //  		// 
    [types.GET_SELLERS](state,data){
        state.seller = data;
    },
    [types.showDetail](state){
        state.detailShow = true;
    },
    [types.hideDetail](state){
        state.detailShow = false;
    },
}
export default{
    state,
    getters,
    actions,
    mutations
}


