<template>
    <div class="cart">
        <div class="mins" @click='mins' v-if="food.count">
            <i class="iconfont icon-jianhao"></i>
        </div>
        <div class="count" v-if="food.count">{{food.count}}</div>
        <div class="adds">
            <i class="iconfont icon-jiahao" @click.stop.prevent='add($event)'></i>
        </div>
    </div>
</template>
<script>
    import Vue from 'vue';
    export default {
        props:['food'],
        methods:{
            add(e){
                clearTimeout(this.timer);
                this.timer = setTimeout( () => {
                    if( !e._constructed ){
                        return;
                    }
                },0)
                if(!this.food.count){
                    Vue.set(this.food,'count',1);
                }else{
                    this.food.count++;
                }
                // console.log(666)
            },
            mins(){
                if(this.food.count){
                    this.food.count--;
                }
                // console.log(77)
            },
            
        }
    }
</script>

<style lang="less" scoped>
    .cart{
        height: 0.48rem;
        width: 1.44rem;
        line-height: 0.48rem;
        display: flex;
        .mins{
            flex:1;
            text-align: center;
            i{
                line-height: 0.48rem;
                font-size: 0.48rem;
                color:#00a0dc;
                vertical-align: top;
            }
        }
        .count{
            flex:1;
            line-height: 0.48rem;
            font-size: 0.2rem;
            color:rgb(147,153,159);
            text-align: center;
        }
        .adds{
            flex:1;
            text-align: center;
            i{
                line-height: 0.48rem;
                font-size: 0.48rem;
                color:#00a0dc;
            }
        }
    }
</style>



