<template>
    <div class="select">
        <div class="types">
            <div class="type-item first"  @click='select(2)'>
                <span class="first-left">{{foodtypes[2]}}</span>
                <span class="first-right">{{food.ratings?food.ratings.length:0}}</span>
            </div>
            <div class="type-item second"  @click='select(0)'>
                <span class="second-left">{{foodtypes[0]}}</span>
                <span class="second-right">{{goodCount}}</span>
            </div>
            <div class="type-item third"  @click='select(1)'>
                <span class="third-left">{{foodtypes[1]}}</span>
                <span class="third-right">{{goodCount}}</span>
            </div>
        </div>
        <div class="read-only" @click = 'toggle'>
            <i class="iconfont icon-duihao"></i>
            <span></span>
        </div>
    </div>
</template>
<script>
    import { mapGetters } from 'vuex';
    export default {
        computed:{
            ...mapGetters([
                'foodtypes',
                'food',
                'selectFoodRatings'
            ]),
            goodCount(){
                if(this.food.ratings){
                    return this.food.ratings.filter(rating=>{
                        return rating.rateType === 0 ;
                    }).length;
                }else{
                    return 0;
                }
            },
            badCount(){
               if(this.food.ratings){
                    return this.food.ratings.filter(rating=>{
                        return rating.rateType === 1 ;
                    }).length;
                }else{
                    return 0;
                }
            }
        },
        // 
        created(){
            this.$store.dispatch('changefoodTypes',['','','']);
        },
        methods:{
            select(n){
                this.$store.dispatch('foodselect',n);
            },
            toggle(){
                 this.$store.dispatch('foodtoggle');
            }
        }
    }
</script>
<style lang="less" scoped>
    .select{
        .types{
            margin-top:0.12rem;
            width: 100%;
            height: auto;
            box-sizing: border-box;
            padding: 0.36rem 0;
            display: flex;
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            .type-item{
                width: 1.2rem;
                height: 0.64rem;
                box-sizing: border-box;
                padding:0.16rem 0.24rem;
                margin-right: 0.16rem;
                border-radius: 0.04rem;
                background: yellow;
                display: table;
            }
            .first{
                background-color: rgb(0,160,220);
                .first-left{
                    font-size: 0.24rem;
                    line-height: 0.32rem;
                    color:rgb(255,255,255);
                    display: table-cell;
                    vertical-align: middle;
                }
                .first-right{
                    display: table-cell;
                    vertical-align: middle;
                    font-size: 0.16rem;
                    line-height: 0.32rem;
                    color:rgb(255,255,255);
                }
            }
            .second{
                background-color: rgba(0,160,220,0.2);
                .second-left{
                    font-size: 0.24rem;
                    line-height: 0.32rem;
                    color:rgb(77,85,93);
                    display: table-cell;
                    vertical-align: middle;
                }
                .second-right{
                    display: table-cell;
                    vertical-align: middle;
                    font-size: 0.16rem;
                    line-height: 0.32rem;
                    color:rgb(77,85,93);
                }
            }
            .third{
                background-color: rgba(77,85,93,0.2);
                .third-left{
                    font-size: 0.24rem;
                    line-height: 0.32rem;
                    color:rgb(77,85,93);
                    display: table-cell;
                    vertical-align: middle;
                }
                .third-right{
                    display: table-cell;
                    vertical-align: middle;
                    font-size: 0.16rem;
                    line-height: 0.32rem;
                    color:rgb(77,85,93);
                }
            }
        }
        .read-only{
            width: 100%;
            height: auto;
            box-sizing: border-box;
            padding: 0.24rem 0;
            i{
                font-size: 0.48rem;
                color: rgb(147,153,159);
                line-height: 0.48rem;
            }
            span{
                font-size: 0.24rem;
                color: rgb(147,153,159);
                line-height: 0.48rem; 
                margin-left: 0.08rem;
                vertical-align: top;
            }
        }
    }
</style>



