<template>
  <div class="seller" ref="seller">
    <div>
        <div class="seller-info">
            <div class="info-top">
                <div class="info-top-left">
                    <p>{{seller.name}}</p>
                    <div class="total-rating">
                        <star :score = 'seller.score' :size="'middle'"></star>
                        <span class="ratingCount">({{seller.ratingCount}})</span>
                        <span class="sellCount">{{seller.sellCount}}</span>
                    </div>
                </div>
                <div class="info-top-right">
                    <i class="iconfont icon-xin"></i>
                    <p></p>
                </div>
            </div>
            <div class="info-bottom">
                <div class="bottom-item">
                    <p class="bottom-item-top"></p>
                    <p class="bottom-item-bottom">
                        {{seller.minPrice}}
                        <span></span>
                    </p>
                </div>
                <div class="bottom-item">
                    <p class="bottom-item-top"></p>
                    <p class="bottom-item-bottom">
                        {{seller.deliveryPrice}}
                        <span></span>
                    </p>
                </div>
                <div class="bottom-item no-right">
                    <p class="bottom-item-top"></p>
                    <p class="bottom-item-bottom">
                        {{seller.deliveryTime}}
                        <span></span>
                    </p>
                </div>
                
            </div>
        </div>
        <div class="bulletin">
            <p class="bulletin-title"></p>
            <p class="bulletin-info">{{seller.bulletin}}</p>
            <div class="bulletin-detail" v-if="seller.supports">
                <div class="info-item">
                    <div class="icon">
                        <img src="../../../static/imgs/decrease.png">
                    </div>
                    <p>{{seller.supports[0].description}}</p>
                </div>
                <div class="info-item">
                    <div class="icon">
                        <img src="../../../static/imgs/discount.png">
                    </div>
                    <p>{{seller.supports[1].description}}</p>
                </div>
                <div class="info-item">
                    <div class="icon">
                        <img src="../../../static/imgs/guarantee.png">
                    </div>
                    <p>{{seller.supports[2].description}}</p>
                </div>
                <div class="info-item">
                    <div class="icon">
                        <img src="../../../static/imgs/invoice.png">
                    </div>
                    <p>{{seller.supports[3].description}}</p>
                </div>
                <div class="info-item">
                    <div class="icon">
                        <img src="../../../static/imgs/special.png">
                    </div>
                    <p>{{seller.supports[4].description}}</p>
                </div>
            </div>
        </div>
        <div class="seller-img">
            <p class="img-title"></p>
            <div ref="picture">
                <ul ref="picList">
                    <li v-for="(item,i) in seller.pics" :key="i">
                        <img :src="item" alt="">
                    </li>
                </ul>
            </div>
        </div>
        <div class="seller-detail">
            <p class="seller-datail-title"></p>
            <div class="seller-detail-item" v-for="(item,index) in seller.infos" :key="index">
                {{item}}
            </div>
        </div>
    </div>   
  </div>
</template>
<script>
    import BScroll from 'better-scroll';
    import { mapGetters } from 'vuex';
    import star from '../star/star.vue';
    export default{
        components:{
            star
        },
        computed:{
            ...mapGetters([
            'seller',
            ]),
        },
        created(){
            this.$store.dispatch('getGoods');
            this.$nextTick( ()=>{
                setTimeout(() =>{
                     this._menuBS();    
                }, 100);   
            });
        },
        watch:{
            seller(){
                let picWidth=120;
                let margin=6;
                let width=(picWidth+margin)*this.seller.pics.length-margin;
                this.$refs.picList.style.width=width+"px";
                if(!this.picBScroll){
                    console.log(this.$refs.picture);
                    this.picBScroll=new BScroll(this.$refs.picture,{
                        scrollX:true
                    });
                }else{
                    this.picBScroll.refresh();
                }
            }
        },
        methods:{
             _menuBS(){
                let sellerBScroll = new BScroll(this.$refs.seller , {click:true} );
            }
        }
    }
</script>
<style lang="less">
    .seller{
        overflow: hidden;
        position: absolute;
        top:3.48rem;
        bottom: 1rem;
        left: 0;
        right: 0;
        background: #f3f5f7;
        .seller-info{
            background: white;
            box-sizing: border-box;
            padding: 0 0.36rem;
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            margin-bottom: 0.32rem;
            .info-top{
            box-sizing: border-box;
            padding: 0.36rem 0;
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            display: flex;
            justify-content:space-between;
            .info-top-left{
                p{
                    font-size: 0.28rem;
                    color: rgb(7, 17, 27);
                    height: 0.28rem;
                    line-height: 0.28rem;
                    margin-bottom: 0.16rem;
                    text-align: left;
                }
                .total-rating{
                    text-align: left;
                    display:flex;
                    height: 0.36rem;
                    .ratingCount{
                        margin-left: 0.16rem;
                        line-height: 0.42rem;
                        height: 0.36rem;
                        color: rgb(77,85,93);
                        font-size: 0.2rem;
                    }
                    .sellCount{
                        line-height: 0.42rem;
                        height: 0.36rem;
                        color: rgb(77,85,93);
                        font-size: 0.2rem;
                        margin-left: 0.24rem;
                    }
                }
            }
            .info-top-right{
                i{
                    height: 0.48rem;
                    line-height: 0.48rem;
                    color:rgb(240,20,20);
                    font-size: 0.48rem;
                }
                p{
                    margin-top: 0.08rem;
                    font-size: 0.2rem;
                    color:rgb(77,85,93);
                    line-height: 0.2rem;
                    height: 0.2rem;
                }
            }
            }
            .info-bottom{
                width: 100%;
                height: auto;
                box-sizing: border-box;
                padding: 0.36rem 0 ;
                display: flex;
                .bottom-item{
                    flex: 1;
                    border-right: 0.02rem solid rgba(7, 17, 27,0.1);
                    text-align: center;
                    .bottom-item-top{
                        font-size: 0.2rem;
                        color: rgb(147,153,159);
                        line-height: 0.2rem;
                        height: 0.2rem;
                        margin-bottom: 0.08rem;
                        vertical-align: middle;
                    }
                    .bottom-item-bottom{
                        font-size: 0.48rem;
                        color: rgb(7,17,27);
                        line-height: 0.48rem;
                        font-weight: 200;
                        height: 0.48rem;
                        vertical-align: middle; 
                        span{
                            font-size: 0.2rem;
                        }
                    }
                }
                .no-right{
                        border-right: 0;
                }
            }
        }
        .bulletin{
            width: 100%;
            height: auto;
            box-sizing: border-box;
            padding: 0.36rem 0.36rem 0 0.36rem;
            background: white;
            margin-bottom: 0.32rem;
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            .bulletin-title{
                font-size: 0.28rem;
                color: rgb(7, 17, 27);
                height: 0.28rem;
                line-height: 0.28rem;
                margin-bottom: 0.16rem;
                text-align: left;
            }
            .bulletin-info{
                width: 100%;
                height: auto;
                box-sizing: border-box;
                padding: 0 0.24rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(240,20,20);
                line-height: 0.48rem;
                text-align: left;
                margin-bottom: 0.32rem;
            }
            .bulletin-detail{
                width: 100%;
                height: auto;
                .info-item{
                display:flex;
                border-top: 0.02rem solid rgba(7, 17, 27,0.1);
                box-sizing: border-box;
                padding: 0.32rem 0.24rem;
                .icon{
                    width: 0.32rem;
                    height: 0.32rem;
                    img{
                        width: 100%;
                        height:100%;
                    }
                }
                p{
                    margin-left:0.12rem;
                    font-size: 0.24rem;
                    line-height: 0.36rem;
                    font-weight: 200;
                    color:rgb(7,17,27);
                }
                }
            }
        }
        .seller-img{
            background: white;
            box-sizing: border-box;
            padding:0.36rem 0 0.36rem 0.36rem;
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            margin-bottom: 0.32rem;
            .img-title{
                font-size: 0.28rem;
                color: rgb(7, 17, 27);
                height: 0.28rem;
                line-height: 0.28rem;
                margin-bottom: 0.24rem;
                text-align: left;
            }
            div{
                ul{
                white-space: nowrap;
                    li{
                        display: inline-block;
                        img{
                            width: 2.4rem;
                            height: 1.8rem;
                            margin:0.24rem 0.12rem 0.36rem 0;
                        }
                        &:last-child{
                            margin:0
                        }
                    }
                }
            }    
        }
        .seller-detail{
            background: white;
            box-sizing: border-box;
            padding:0.36rem 0.36rem 0 0.36rem;
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            .seller-datail-title{
                font-size: 0.28rem;
                color: rgb(7, 17, 27);
                height: 0.28rem;
                line-height: 0.28rem;
                margin-bottom: 0.24rem;
                text-align: left;
            }
            .seller-detail-item{
                border-top: 0.02rem solid rgba(7, 17, 27,0.1);
                box-sizing: border-box;
                padding:0.32rem 0.24rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(7, 17, 27);
                line-height: 0.32rem;
                text-align: left;
            }
        }

    }
</style>




