import types from  '../types.js';
import axios from 'axios';

var state = {
    goods:[],
    food:{},
    // 
    foodshow:false,
    foodtypes:['','',''],	//0 1  2 
    selectFoodRatings:[],//
    selectFoodType:2,
    onlyFoodContent:true,
    //shopDetail
    shopshow:false
}
var getters = {
    goods(state){
        return state.goods;
    },
    food(state){
        return state.food;
    },
    shopshow(state){
        return state.shopshow;
    },
    foodshow(state){
        return state.foodshow;
    },
    foodtypes(state){
        return state.foodtypes;
    },
    selectFoodRatings(state){
        return state.selectFoodRatings;
    },
    selectFoodType(state){
        return state.selectFoodType;
    },
    onlyFoodContent(state){
        return state.onlyFoodContent;
    }

}
var actions = {
    getGoods({commit,state}){
        // axios.get('/api/goods').then((res)=>{
            axios.get('./data.json').then((res)=>{
            // console.log(res.data);
            // if(res.data.errno === 0){
                commit(types.GET_GOODS,res.data.goods);
            // }
        });
    },
    showFood({commit,state},f){
        commit(types.GET_FOOD,f);
    },
    showShop({commit}){
        commit(types.SHOWSHOPDETAIL);
    },
    hidefoodDetail({commit}){
        commit ( types.hidefood );
    },
     // 
    foodselect({commit},n){
        commit(types.FOODTYPES,n);
    },
    // 
    foodtoggle({commit}){
        commit(types.TOGGLE);
    },
    //
    changefoodTypes({commit},_types){
       commit(types.CHANGE_FoodTYPES,_types);
   },
}
var mutations = {
    [types.GET_GOODS](state,data){
        state.goods = data;
    },
    [types.GET_FOOD](state,f){
        state.food = f;
        state.foodshow = true;
        state.selectFoodRatings = state.food.ratings;
    },
    [types.hidefood](state){
        state.foodshow = false;
    },
    [types.SHOWSHOPDETAIL](state){
        state.shopshow=!state.shopshow;
    },
     // 
     [types.CHANGE_FoodTYPES](state,_types){
        state.types = _types;
        state.selectFoodRatings = state.food.ratings;
    },
    // 
    [types.FOODTYPES](state,type){
        state.selectFoodType = type;
        state.selectFoodRatings = state.food.ratings.filter(rating=>{
            if(state.selectFoodType === 2){
                return true;
            }else{
                return rating.rateType == state.selectFoodType;
            }
        })
        console.log("------------",state.selectFoodRatings);
    },
    // 
    [types.ONLYFOODCONTENT](state){
        state.onlyFoodContent = !state.onlyFoodContent;
        state.selectFoodRatings = state.food.ratings.filter(rating=>{
            //    
            if(state.onlyFoodContent && !rating.text){
                return false;
            }else if( state.selectFoodType===2){
                return true;
            }else{
                return rating.rateType == state.selectFoodType;
            }
        })
    }
}
export default{
    state,
    getters,
    actions,
    mutations
}


