<template>
  <div class="ratings" ref="ratings">
      <div>
         <div class="total-ratings">
            <div class="ratings-left">
                <p class="score">{{seller.score}}</p>
                <p class="score-info"></p>
                <p class="rankRate">{{seller.rankRate}}</p>
            </div>
            <div class="ratings-right">
                    <div class="right-item">
                        <div class="score-describe"></div>
                        <div class="star">
                            <star :score = 'seller.foodScore' :size="'small'"></star>
                        </div>
                        <div class="score-every">{{seller.foodScore}}</div>
                    </div>
                    <div class="right-item">
                        <div class="score-describe"></div>
                        <div class="star">
                            <star :score = 'seller.serviceScore' :size="'small'"></star>
                        </div>
                        <div class="score-every">{{seller.serviceScore}}</div>
                    </div>
                    <div class="right-item delivery">
                        <div class="score-describe"></div>
                        <div class="delivery-time">{{seller.deliveryTime}}</div>
                    </div>
            </div>
        </div>
        <div class="food-comment">
            <!-- <div class="types">
                <div class="type-item first">
                    <span class="first-left"></span>
                    <span class="first-right">57</span>
                </div>
                <div class="type-item second">
                    <span class="second-left"></span>
                    <span class="second-right">47</span>
                </div>
                <div class="type-item third">
                    <span class="third-left"></span>
                    <span class="third-right">10</span>
                </div>
            </div>
            <div class="read-only">
                <i class="iconfont icon-duihao"></i>
                <span></span>
            </div> -->
            <ratingSelect></ratingSelect>
        </div>
        <div class="comment-datails" >
            <div class="everyone" v-for="(item,i) in selectRatings" :key="i">
                <div class="user-img">
                    <img :src="item.avatar">
                </div>
                <div class="everyone-right">
                    <div class="user">
                        <div class="username">{{item.username}}</div>
                        <div class="rateTime">{{item.rateTime|changeTime}}</div>
                    </div>
                    <div class="everyone-delivery"> 
                         <star :score = 'item.score' ></star>
                         <span v-if="item.deliveryTime">{{item.deliveryTime}}</span>
                    </div>
                    <p class="text">{{item.text}}</p>
                    <div class="everyone-bottom" v-if="item.recommend!=[]?true:false">
                        <i class="iconfont" :class="item.rateType==0?'icon-damuzhion':'icon-down'"></i>
                        <div class="everyone-bottom-right">
                            <div class="recommend-item" v-for="(value,index) in item.recommend" :key="index">
                                {{value}}
                            </div>
                        </div>
                        
                    </div>
                </div>
            </div>
        </div> 
      </div>    
  </div>
</template>
<script>
    import BScroll from 'better-scroll';
    import { mapGetters } from 'vuex';
    import star from '../star/star.vue';
    import ratingSelect from '../ratingSelect/ratingSelect.vue';
    export default{
        components:{
            star,
            ratingSelect
        },
        computed:{
            ...mapGetters([
            'seller',
            'ratings',
            'selectRatings'
            ]),
        },
        watch:{
            selectRatings(){
                this.$nextTick(()=>{
                    this.ratingsBS();
                });
            }
        },
        methods:{
            ratingsBS(){
                let ratingsBScroll = new BScroll(this.$refs.ratings,{
                    click:true
                })
            }
        },
        created(){
            this.$store.dispatch('getSeller');
            this.$store.dispatch('getRatings');
        },
        filters:{
            changeTime(data){
                let date = new Date(data);
                let year =date.getFullYear();
                let month = date.getMonth()+1;
                if(month<10){
                    month="0"+month;
                }
                let day = date.getDate();
                let hour = date.getHours();
                let minutes = date.getMinutes();
                let str = ""+year+"-"+month+"-"+day+" "+hour+":"+minutes;
                return str;
            }
        }
    }
</script>
<style lang="less" scoped>
    .ratings{
        overflow: hidden;
        position: absolute;
        top:3.48rem;
        bottom: 1rem;
        left: 0;
        right: 0;
        background: #f3f5f7;
        .total-ratings{
            width: 100%;
            height: auto;
            background: white;
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            display: flex;
            box-sizing: border-box;
            padding: 0.36rem 0 0.36rem 0.36rem;
            margin-bottom: 0.32rem;
            .ratings-left{
                width: 2.75rem;
                box-sizing: border-box; 
                padding-top: 0.12rem;
                border-right: 0.01rem solid rgba(7, 17, 27,0.1);
                .score{
                    height: 0.56rem;
                    color:rgb(255,153,0);
                    font-size: 0.48rem;
                    line-height: 0.56rem;
                }
                .score-info{
                    height: 0.24rem;
                    color:rgb(7,17,27);
                    font-size: 0.24rem;
                    line-height: 0.24rem;
                    margin-top: 0.12rem;
                    margin-bottom: 0.16rem;
                }
                .rankRate{
                    height: 0.2rem;
                    color:rgb(77,85,93);
                    font-size: 0.2rem;
                    line-height: 0.2rem;
                    margin-bottom: 0.12rem;
                }
            }
            .ratings-right{
                width: 4.74rem;
                // background: green;
                box-sizing: border-box;
                padding: 0.12rem 0.48rem;
                .right-item{
                    margin-bottom: 0.16rem;
                    // background: pink;
                    height: 0.36rem;
                    line-height: 0.36rem;
                    display: flex;
                    .score-describe{
                        font-size:0.24rem;
                        color:rgb(7,17,27);
                        height: 0.36rem;
                        line-height: 0.36rem;
                        margin-right: 0.24rem;
                    }
                    .score-every{
                        height: 0.36rem;
                        font-size:0.24rem;
                        line-height: 0.36rem;
                        color:rgb(255,153,0);
                        margin-left: 0.24rem;
                    }
                    
                } 
                .delivery{
                    margin-bottom: 0;
                    .delivery-time{
                        font-size:0.24rem;
                        color:rgb(147,153,159);
                        height: 0.36rem;
                        line-height: 0.36rem;
                        margin-right: 0.24rem;
                    }   
                }
            }
        }
        .food-comment{
            width:100%;
            height: auto;
            text-align: left;
            background:white;
            box-sizing: border-box;
            padding: 0.36rem;
            padding-bottom: 0;
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            .types{
                margin-top:0.12rem;
                width: 100%;
                height: auto;
                box-sizing: border-box;
                padding-bottom: 0.36rem;
                display: flex;
                border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
                .type-item{
                    width: 1.2rem;
                    height: 0.64rem;
                    box-sizing: border-box;
                    padding:0.16rem 0.24rem;
                    margin-right: 0.16rem;
                    border-radius: 0.04rem;
                    background: yellow;
                    display: table;
                }
                .first{
                    background-color: rgb(0,160,220);
                    .first-left{
                        font-size: 0.24rem;
                        line-height: 0.32rem;
                        color:rgb(255,255,255);
                        display: table-cell;
                        vertical-align: middle;
                    }
                    .first-right{
                        display: table-cell;
                        vertical-align: middle;
                        font-size: 0.16rem;
                        line-height: 0.32rem;
                        color:rgb(255,255,255);
                    }
                }
                .second{
                    background-color: rgba(0,160,220,0.2);
                    .second-left{
                        font-size: 0.24rem;
                        line-height: 0.32rem;
                        color:rgb(77,85,93);
                        display: table-cell;
                        vertical-align: middle;
                    }
                    .second-right{
                        display: table-cell;
                        vertical-align: middle;
                        font-size: 0.16rem;
                        line-height: 0.32rem;
                        color:rgb(77,85,93);
                    }
                }
                .third{
                    background-color: rgba(77,85,93,0.2);
                    .third-left{
                        font-size: 0.24rem;
                        line-height: 0.32rem;
                        color:rgb(77,85,93);
                        display: table-cell;
                        vertical-align: middle;
                    }
                    .third-right{
                        display: table-cell;
                        vertical-align: middle;
                        font-size: 0.16rem;
                        line-height: 0.32rem;
                        color:rgb(77,85,93);
                    }
                }
            }
            .read-only{
                width: 100%;
                height: auto;
                box-sizing: border-box;
                padding: 0.24rem 0;
                i{
                    font-size: 0.48rem;
                    color: rgb(147,153,159);
                    line-height: 0.48rem;
                }
                span{
                    font-size: 0.24rem;
                    color: rgb(147,153,159);
                    line-height: 0.48rem; 
                    margin-left: 0.08rem;
                    vertical-align: top;
                }
            }
        }
        .comment-datails{
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            width: 100%;
            height: auto;
            box-sizing: border-box;
            padding:0 0.36rem;
            background: white;
            .everyone{
                width: 100%;
                height: auto;
                box-sizing: border-box;
                padding: 0.36rem 0;
                border-bottom: 0.01rem solid rgba(7, 17, 27,0.1);
                display: flex;
                .user-img{
                    width: 0.56rem;
                    height: 0.56rem;
                    margin-right: 0.24rem;
                    img{
                        width: 0.56rem;
                        height: 0.56rem;
                        border-radius: 100%;
                    }
                }
                .everyone-right{
                    width: 100%;
                    height: auto;
                    .user{
                        display: flex;
                        justify-content: space-between;
                        .username{
                        font-size: 0.2rem;
                        height: 0.24rem;
                        line-height: 0.24rem;
                        color: rgb(7,17,27);
                        text-align: left;
                        }
                        .rateTime{
                        font-size: 0.2rem;
                        height: 0.24rem;
                        line-height: 0.24rem;
                        font-weight: 200;
                        color: rgb(147,153,159);
                        }
                        
                    }
                    .everyone-delivery{
                        width: 100%;
                        margin: 0.08rem 0 0.12rem 0;
                        display: flex;
                        span{
                            margin-left: 0.12rem;
                            height: 0.24rem;
                            font-size: 0.2rem;
                            line-height: 0.24rem;
                            vertical-align: bottom;
                        }
                    }
                    .text{
                        line-height: 0.36rem;
                        font-size: 0.24rem;
                        color:rgb(7,17,27);
                        margin-bottom: 0.16rem;
                        text-align: left;
                    }
                    .everyone-bottom{
                        width: 100%;                          
                        height: auto;
                        display: flex;
                        i{
                            font-size: 0.24rem;
                            line-height: 0.32rem;
                            &.icon-damuzhion{
                                color: rgb(0,160,220);
                            }
                            &.icon-down{
                                color: rgb(183,187,191);
                            }
                        }
                        .everyone-bottom-right{
                            width: 5rem; 
                            height: auto;;
                            display: flex;
                            flex-wrap: wrap;
                            .recommend-item{
                                border: 0.01rem solid rgba(7, 17, 27,0.1);
                                margin-bottom: 0.1rem;
                                margin-left: 0.16rem;
                                width: 1.26rem;
                                height: 0.32rem;
                                box-sizing: border-box;
                                padding: 0 0.12rem;
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis;
                                color: rgb(147,153,159);
                                line-height: 0.32rem;
                                font-size: 0.18rem;
                            }
                        }
                        
                    }
                }
            }
        }
    }
</style>


