<template>
  <div class="goods">
      <div class="menu-wrap" ref="left">
          <div>
            <div v-for="(item,i) in goods" :key="i" class="good-item" @click="selectMenu(i)" :class = "{'current':i===currentIndex}">
                <div>{{item.name}}</div>
            </div>
          </div>
      </div>
      <div class="foods-wrap" ref="right">
          <div>
            <div class="type-item food-list-hook" v-for="(item,index) in goods" :key="index" >
                <div class="type-title">
                    {{item.name}}
                </div>
                <div class="type-content">
                    <div class="food-item " v-for="(food,i) in item.foods" :key="i">
                        <div class="item-img">
                            <img :src="food.icon" @click="showFood(food)">
                        </div>
                        <div class="item-right">
                            <p class="food-name">{{food.name}}</p>
                            <p class="food-description" v-show="food.description">{{food.description}}</p>
                            <p class="food-detail">
                                {{food.sellCount}}
                                <span>{{food.rating}}%</span>
                            </p>
                            <div class="food-price">
                                <span class="price">{{food.price}}</span>
                                <span class="food-oldPrice" v-show="food.oldPrice">{{food.oldPrice}}</span>
                                <div class="buy">
                                    <cartControl :food='food'></cartControl>
                                </div>
                            </div>
                        </div>  
                    </div>
                </div>
            </div>
          </div>
      </div>
      <foodDetail></foodDetail>
  </div>
</template>
<script>
    import BScroll from 'better-scroll';
    import { mapGetters } from 'vuex';
    import foodDetail from '../foodDetail/foodDetail.vue'
    import cartControl from '../cartControl/cartcontrol.vue'
    export default{
        data(){
            return{
                listHeight:[],
                scrollY:0
            }
        },
        components:{
            foodDetail,
            cartControl
        },
        created(){
            this.$store.dispatch('getGoods');
        },
        watch:{
            goods(){
                this.$nextTick( () => {
                    this._menuBS();
                    this.calcHeight();
                    if( !this.foodBScroll ){
                        this.foodBScroll = new BScroll(this.$refs.right,{click:true,probeType:3});
                        this.foodBScroll.on('scroll',(pos)=>{
                            this.scrollY = Math.abs( Math.round(pos.y))+2;
                        })
                    }else{
                        this.foodBScroll.refresh();
                    }
                });
            }
        },
        computed:{
            ...mapGetters([
            'goods'
            ]),
            currentIndex(){
                for(let i =0;i<this.listHeight.length;i++){
                    let height1 = this.listHeight[i];
                    let height2 = this.listHeight[i+1];
                    // if(this.scrollY>=height1&&this.scrollY<height2){
                        if(this.scrollY<height2-1){
                        return i;
                    }
                }
                return 0;
            }
        },
        methods:{
            _menuBS(){
                let menuBScroll = new BScroll(this.$refs.left , {click:true} );
         
            },
            calcHeight(){
                let foodList = this.$refs.right.getElementsByClassName('food-list-hook');
                let height = 0;
                this.listHeight.push(height);
                for(let i=0;i<foodList.length;i++){
                    height+=foodList[i].clientHeight;
                    this.listHeight.push(height);
                }
            },
            selectMenu(i){
                let foodList = this.$refs.right.getElementsByClassName('food-list-hook');
                let el = foodList[i];
                this.foodBScroll.scrollToElement(el,300);
            },
            showFood(food){
                this.$store.dispatch('showFood',food);
            }

        }
    }
</script>
<style lang="less" scoped>
    .goods{
        position: absolute;
        top:3.48rem;
        bottom: 0.9rem;
        display: flex;
        width: 100%;
        overflow: hidden;
        .menu-wrap{
            flex: 0 0 1.6rem;// 
            width:1.6rem;
            background:#f3f5f7;
           .good-item{
                display: table;
                height: 1.08rem;
                width: 1.6rem;
                color: rgb(77,85,93); 
                box-sizing: border-box;
                padding: 0 0.24rem;  
                &.current{
					color:black;
					background: white;
				}
               div{ 
                border-bottom: 0.02rem solid rgba(7,17,27,0.1);
                font-size: 0.24rem;
                font-weight: 200;
                line-height: 0.28rem;
                position: relative;
                text-align: left;
                display: table-cell;               
                vertical-align: middle; 
               }
           }
        }
        .foods-wrap{
            flex:1;
            overflow: hidden;
            .type-item{
                width: 100%;
                height: auto;
                .type-title{
                    height: 0.52rem;
                    border-left: 0.06rem solid #d9dde1;
                    background-color: #f3f5f7;
                    font-size: 0.24rem;
                    color: rgb(147,153,159);
                    line-height: 0.52rem;
                    box-sizing: border-box;
                    padding-left: 0.28rem;
                    text-align: left;
                }
                .type-content{
                    width: 100%;
                    height: auto;
                    box-sizing: border-box;
                    padding: 0 0.36rem ;
                    .food-item{
                        width: 100%;
                        height: auto;
                        border-bottom: 0.01rem solid rgba(7,17,27,0.1);
                        display: flex;
                        box-sizing: border-box;
                        padding: 0.36rem 0;
                        .item-img{
                            width: 1.14rem;
                            height: 1.14rem;
                            img{
                                width: 100%;
                                height: 100%;
                            }
                        }
                        .item-right{
                            width: 4.06rem;
                            height: auto;
                            box-sizing: border-box;
                            padding-left: 0.2rem;
                            text-align: left;
                            .food-name{
                                font-size: 0.28rem;
                                color:rgb(7,17,27);
                                line-height: 0.28rem;
                                margin-top: 0.04rem;
                                margin-bottom: 0.16rem;
                            }
                            .food-description{
                                font-size: 0.2rem;
                                color:rgb(147,153,159);
                                line-height: 0.2rem;
                                margin-bottom: 0.16rem;
                            }
                            .food-detail{
                                font-size: 0.2rem;
                                color:rgb(147,153,159);
                                line-height: 0.2rem;
                                span{
                                    margin-left: 0.24rem;
                                }
                            }
                            .food-price{
                                text-align: left;
                                margin-top: 0.16rem;
                                .price{
                                    font-size: 0.28rem;
                                    color:red;
                                    font-weight: 700;
                                    line-height: 0.48rem;
                                }
                                .food-oldPrice{
                                    font-size: 0.2rem;
                                    text-decoration: line-through;
                                    color:rgb(147,153,159);
                                    font-weight: 700;
                                    line-height: 0.48rem;
                                    margin-left: 0.16rem;
                                }
                                .buy{
                                    width: 1.44rem;
                                    height: 0.48rem;
                                    float: right;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
</style>


