<template>
  <transition enter-active-class="animated bounceInLeft" leave-active-class="animated bounceOutRight">
      <div class="detail" v-show ='detailShow'>
          <p @click='hideDetail'>{{seller.name}}</p>
          <star :score = 'seller.score' :size="'big'"></star>
          <div class="decrease">
              <span></span><div></div><span></span>
          </div>
        <!--  -->
        <div class="decrease-info" v-if="seller.supports"> 
            <div class="info-item">
                <div class="icon">
                    <img src="../../../static/imgs/decrease.png">
                </div>
                <p>{{seller.supports[0].description}}</p>
            </div>
            <div class="info-item">
                <div class="icon">
                    <img src="../../../static/imgs/discount.png">
                </div>
                <p>{{seller.supports[1].description}}</p>
            </div>
            <div class="info-item">
                <div class="icon">
                    <img src="../../../static/imgs/guarantee.png">
                </div>
                <p>{{seller.supports[2].description}}</p>
            </div>
            <div class="info-item">
                <div class="icon">
                    <img src="../../../static/imgs/invoice.png">
                </div>
                <p>{{seller.supports[3].description}}</p>
            </div>
            <div class="info-item">
                <div class="icon">
                    <img src="../../../static/imgs/special.png">
                </div>
                <p>{{seller.supports[4].description}}</p>
            </div>
        </div>
        <!--  -->
        <div class="bulletin">
            <span></span><div></div><span></span>
        </div>
        <!--  -->
        <div class="bulletin-info">
            {{seller.bulletin}} 
        </div>
        <!--  -->
        <div v-show ='detailShow' @click='hideDetail' class="quit">
            <i class="iconfont off">&#xe648;</i>
        </div>        
      </div>
  </transition>
</template>
<script>
    import { mapGetters } from 'vuex';
    import animate from 'animate.css';
    import star from '../star/star.vue';
    export default{
        components:{
            star
        },
        computed:{
            ...mapGetters([
                'seller',
                'detailShow'
            ])
        },
        methods:{
            hideDetail(){
                this.$store.dispatch('hideDetail');
            }
        }
    }
</script>
<style lang="less" scoped>
    .detail{
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
        background-color: rgba(7, 17, 27, 0.8);
        box-sizing: border-box;
        padding: 1.28rem 0.72rem 0.64rem 0.72rem;
        p{
            font-size: 0.32rem;
            font-weight:700;
            color:rgb(255, 255, 255);
            line-height: 0.32rem;
            margin-bottom: 0.32rem;
        }
        .decrease,.bulletin{
            width: 100%;
            height: 0.28rem;
            margin-top: 0.56rem;
            display: flex;
            justify-content: space-between;
            div{
                font-size: 0.28rem;
                font-weight: 700;
                color: rgb(255, 255, 255);
                line-height: 0.28rem;
            }
            span{
                display: inline-block;
                width: 2.24rem;
                background-color: rgba(255,255,255,0.2);
                height: 0.04rem;
                margin-top: 0.12rem;
            }   
        }
        .bulletin{
            margin-top:0;
        }
        .decrease-info{
            width: 100%;
            height: auto;
            box-sizing: border-box;
            padding: 0.48rem  0.24rem 0.28rem 0.24rem;
            .info-item{
                height: 0.32rem;
                margin-bottom: 0.24rem;
                display:flex;
                .icon{
                    width: 0.32rem;
                    height: 0.32rem;
                    img{
                        width: 100%;
                        height:100%;
                    }
                }
                p{
                    margin-left:0.12rem;
                    margin-top: 0.04rem; 
                   font-size: 0.24rem;
                   line-height: 0.24rem;
                   font-weight: 200;
                   color:rgb(255,255,255);
                }
            }
        }
        .bulletin-info{
            box-sizing: border-box;
            padding: 0.48rem 0.24rem;
            font-size: 0.24rem;
            font-weight: 200;
            color: rgb(255,255,255);
            line-height: 0.48rem;
            text-align: left;
        }
        .quit{
            position: absolute;
            bottom: 0.64rem;
            left: 47%;
            i{
                font-size: 0.64rem;  
                color: rgba(255,255,255,0.5);
            }
        }
       
	}
</style>



