<template>
  <div class="star" :class='starType'>
      <!-- {{itemClasss}} -->
      <span v-for="(itemClass,index) in itemClasss" :key="index" class="star-item" :class="itemClass"></span>
  </div>
</template>
<script>
    const LENGTH = 5;
    const ON = 'on';
    const HALF = 'half';
    const OFF = 'off';
    export default {
        props:['score','size'],
        computed:{
            starType(){
                return 'star-'+ this.size;
            },
            itemClasss(){
                let result = [];
                let integer = Math.floor( this.score);
                let decimal = this.score - integer;
                for(let i =0;i<integer;i++){
                    result.push( ON );
                }
                if(decimal>0.5){
                    result.push( HALF );
                }
                while( result.length < LENGTH){
                    result.push(OFF)
                }
                return result ;
            }
        }
    }
</script>
<style lang="less" scoped>
    .star{
        font-size: 0.4rem;
        .star-item{
            display:inline-block;
            width:0.24rem;
            height: 0.24rem;
            margin-right: 0.1rem;
            vertical-align: top;
            &.on{
                background-image: url("../../../static/imgs/star_on.png");
                background-size: 0.24rem 0.24rem;
                background-repeat: no-repeat;
            }
            &.half{
                background-image: url("../../../static/imgs/star_half.png");
                background-size: 0.24rem 0.24rem;
                background-repeat: no-repeat;
            }
            &.off{
                background-image: url("../../../static/imgs/star_off.png");
                background-size: 0.24rem 0.24rem;
                background-repeat: no-repeat;
            }
        }
        &.star-big{
            .star-item{
                display: inline-block;
                width: 0.48rem;
                height: 0.48rem;
                margin-right: 0.38rem;
                &.on{
                background-image: url("../../../static/imgs/star_on.png");
                background-size: 0.48rem 0.48rem;
                background-repeat: no-repeat;
                }
                &.half{
                background-image: url("../../../static/imgs/star_half.png");
                background-size: 0.48rem 0.48rem;
                background-repeat: no-repeat;
                }
                &.off{
                background-image: url("../../../static/imgs/star_off.png");
                background-size: 0.48rem 0.48rem;
                background-repeat: no-repeat;
                }
            }
        }
        &.star-small{
            .star-item{
                display: inline-block;
                width: 0.26rem;
                height: 0.26rem;
                margin-right: 0.1rem;
                vertical-align: top;
                &.on{
                background-image: url("../../../static/imgs/star_on.png");
                background-size: 0.26rem 0.26rem;
                background-repeat: no-repeat;
                }
                &.half{
                background-image: url("../../../static/imgs/star_half.png");
                background-size: 0.26rem 0.26rem;
                background-repeat: no-repeat;
                }
                &.off{
                background-image: url("../../../static/imgs/star_off.png");
                background-size: 0.26rem 0.26rem;
                background-repeat: no-repeat;
                }
            }
        }
        &.star-middle{
            .star-item{
                display: inline-block;
                width: 0.36rem;
                height: 0.36rem;
                margin-right: 0.1rem;
                &.on{
                background-image: url("../../../static/imgs/star_on.png");
                background-size: 0.36rem 0.36rem;
                background-repeat: no-repeat;
                }
                &.half{
                background-image: url("../../../static/imgs/star_half.png");
                background-size: 0.36rem 0.36rem;
                background-repeat: no-repeat;
                }
                &.off{
                background-image: url("../../../static/imgs/star_off.png");
                background-size: 0.36rem 0.36rem;
                background-repeat: no-repeat;
                }
            }
        }
        
    }
</style>




