<template>
  <div class="header">
    <div class="bg">
        <img :src="seller.avatar">
    </div>
    <div class="header-top">
        <div class="header-top-left">
            <img :src="seller.avatar">
        </div>
        <div class="header-top-right">
            <div class="top">
                <div class="brand">
                    <img src="../../../static/imgs/brand.png">
                </div>
                <div class="seller-name">{{seller.name}}</div>
            </div>
            <div class="middle">{{seller.description}}/{{seller.deliveryTime}}</div>
            <div class="bottom" v-if="seller.supports">
                <div class="decrease">
                    <img src="../../../static/imgs/decrease.png" >
                </div>
                <p>{{seller.supports[0].description}}</p>
            </div>               
        </div>
        <div class="supports" v-if="seller.supports" @click='showDetail'>
            <!--  -->
            {{seller.supports.length}}<span>&gt;</span>
        </div>
    </div>
    <div class="header-bottom">
        <div class="icon">
            <img src="../../../static/imgs/bulletin.png">
        </div>
        <div class="bulletin">
            {{seller.bulletin}}  
        </div>
        <span>&gt;</span>
    </div>              
  </div>
</template>
<script>
    import { mapGetters, mapActions  } from 'vuex';
    export default {
        computed:{
            ...mapGetters([
            'seller'
            ])
        } ,
        methods:{
            ...mapActions([
				'showDetail'
			])
        }   
    }
</script>
<style scoped lang='less'>
    .header{
        width:100%;
        height: 2.68rem;
        position: relative; 
        background-color: rgba(7,17,27,0.5);
        .bg{
           position: absolute;
           top:0;
           left: 0;
           right: 0;
           bottom:0;
           z-index:-1;
           img{
               width:100%;
               height:100%; 
               filter:blur(0.1rem);
           } 
        }
        .header-top{
            width:100%;
            height: 2.12rem;
            box-sizing: border-box;
            position: relative;
            padding:0.48rem 0.24rem 0.36rem 0.48rem;
            .header-top-left{
                width:1.28rem;
                height:1.28rem;
                float:left;
                img{
                    width:100%;
                    height:100%; 
                    border-radius: 0.04rem;
                }
            }
            .supports{
            position: absolute;
            bottom:0.36rem;
            right: 0.24rem;
            width: 0.86rem;
            height:0.48rem;
            background-color: rgba(0,0,0,0.2);
            border-radius: 0.32rem ;
            font-size: 0.2rem;
            color: rgb(255,255,255);
            font-weight: 200;
            line-height: 0.48rem;
            span{
                margin-left:0.04rem;
                }
            }    
        }  
        .header-bottom{
            width: 100%;
            height: 0.56rem;
            background-color: rgba(7,17,27,0.2);
            box-sizing: border-size;
            padding:0 0.24rem;
            .icon{
                float: left;
                width: 0.44rem;
                box-sizing: border-box;
                padding-top: 0.13rem;
                img{
                    width: 100%;
                }
            }
            .bulletin{
                float: left;
                margin-left: 0.08rem;
                width: 6.22rem;
                height: 0.56rem;
                font-size: 0.2rem;
                color: rgb(255,255,255);
                font-weight: 200;
                line-height:0.56rem;
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
            span{
                margin-right: 0.24rem;
                font-size: 0.2rem;
                color: rgb(255,255,255);
                line-height:0.56rem;
            }
        }   
    } 
    .header-top-right{
        width: 200px;
        height: 100%;
        margin-left:0.32rem;
        float:left;
        .top{
            height:0.36rem;
            margin-top: 0.04rem;
           .brand{
            width: 0.6rem;
            height:0.36rem;
            float: left;
                img{
                    width:100%;
                    height:100%;
                }
            } 
            .seller-name{
                margin-left:0.12rem;
                font-size:0.32rem;
                color:rgb(255,255,255);
                font-weight:bold;
                line-height:0.36rem;
            }
        } 
        .middle{
            font-size: 0.24rem;
            color: rgb(255,255,255);
            font-weight: 200;
            line-height: 0.24rem;
            text-align: left;
            margin-top:0.16rem;
        }  
        .bottom{
            height: 0.24rem;
            margin-top:0.2rem;
            .decrease{
                width: 0.24rem;
                height: 0.24rem;
                float: left;
                img{
                    width: 100%;
                    height: 100%;
                }
            }
            p{
               font-size: 0.2rem;
               color:rgb(255,255,255);
               font-weight:200;
               line-height: 0.24rem;
               margin-left: 0.08rem;
                float: left;
            }
        }
    }
</style>



