<template>
  <div class="food" v-show="foodshow" ref="foodDetail">
    <div>
        <div class="imgshow">
          <img :src="food.image" @click="hideFood">
        </div>
        <div class="food-detail">
            <p class="food-name">{{food.name}}</p>
            <p class="food-sale">
                {{food.sellCount}}
                <span>{{food.rating}}%</span>
            </p>
            <div class="food-price">
                <span class="price">{{food.price}}</span>
                <span class="food-oldPrice" v-if="food.oldPrice">{{food.oldPrice}}</span>
            </div>
            <div class="buy">
                <div class="car" v-show="!food.count" @click = 'addFirst'></div>
                <cartControl :food='food' v-show="food.count"></cartControl> 
            </div>
        </div>
        <div class="food-info" v-show="food.info">
            <span></span>
            <p>{{food.info}}</p>
        </div>
        <div class="food-comment">
            <span></span>
            <foodRatingSelect></foodRatingSelect>
        </div>
        <div class="comment-details">
            <div class="everyone" v-for="(item,i) in selectFoodRatings" :key="i">
                <!-- {{selectFoodRatings}},{{item}} -->
                <div class="ereryone-info" >
                    <div class="info-left">{{item.rateTime|changeTime}}</div>
                    <div class="info-right">{{item.username}}<img :src="item.avatar"></div>
                </div>
                <div class="everoyne-comment">
                    <i class="iconfont" :class="item.rateType==0?'icon-damuzhion':'icon-down'"></i>
                    <span>{{item.text}}</span>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>
<script>    
    import BScroll from 'better-scroll';
    import { mapGetters } from 'vuex';
    import cartControl from '../cartControl/cartcontrol.vue';
    import foodRatingSelect from '../foodRatingSelect/foodRatingSelect.vue';
    import Vue from 'vue';
    export default {
        computed:{
            ...mapGetters([
                'foodshow',
                'food',
                'selectFoodRatings'
            ])
        },
        components:{
            cartControl,
            foodRatingSelect
		},
        watch:{
            food(){
                this.$nextTick(()=>{
                    if( !this.foodBScroll ){
                        this.foodBScroll = new BScroll( this.$refs.foodDetail,{
                            click : true
                        })
                    }else{  
                        this.foodBScroll.refresh();
                    }
                });
            }
        },
        methods:{
            hideFood(){
                this.$store.dispatch('hidefoodDetail');
            },
            addFirst(){
                Vue.set(this.food,'count',1);
            }

        },
        filters:{
            changeTime(data){
                let date = new Date(data);
                let year =date.getFullYear();
                let month = date.getMonth()+1;
                if(month<10){
                    month="0"+month;
                }
                let day = date.getDate();
                let hour = date.getHours();
                let minutes = date.getMinutes();
                let str = ""+year+"-"+month+"-"+day+" "+hour+":"+minutes;
                return str;
            }
        }
    }
</script>
<style lang="less" scoped>
    .food{
        overflow: hidden;
        position: fixed;
        top:0;
        left:0;
        right: 0;
        bottom: 0;
        background: #f3f5f7;
        margin-bottom:1rem;
        .imgshow{
            position: relative;
            width: 100%;
            height: 0;
            padding-top:100%;//padding 
            img{
                position: absolute;
                top:0;
                left: 0;
                width: 100%;
                height: 100%;
            }
        }
        .food-detail{
            width:100%;
            height: auto;
            text-align: left;
            background:white;
            box-sizing: border-box;
            padding: 0.36rem;
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            margin-bottom: 0.32rem;
            position: relative;
            .food-name{
                font-size: 0.28rem;
                font-weight: 700;
                color:rgb(7, 17, 27);
                height: 0.28rem;
                line-height: 0.28rem;
            }
            .food-sale{
                height: 0.2rem;
                font-size: 0.2rem;
                line-height: 0.2rem;
                color: rgb(147,153,159);
                margin-top: 0.16rem;
                margin-bottom: 0.36rem;
            }
            .food-price{
                height: 0.48rem;
                .price{
                    font-size: 0.28rem;
                    color:red;
                    font-weight: 700;
                    line-height: 0.48rem;
                    }
                .food-oldPrice{
                    font-size: 0.2rem;
                    text-decoration: line-through;
                    color:rgb(147,153,159);
                    font-weight: 700;
                    line-height: 0.48rem;
                    margin-left: 0.16rem;
                }
            }
            .buy{
                position: absolute;
                right:0.36rem;
                bottom: 0.4rem;
                width: 1.48rem;
                height: 0.48rem;
                .car{
                    border-radius: 0.24rem;
                    box-sizing: border-box;
                    padding: 0.12rem 0;
                    background: rgb(0,160,220);
                    color: white;
                    line-height: 0.24rem;
                    font-size: 0.2rem;
                    text-align: center;
                }
            }
        }
        .food-info{
            width:100%;
            height: auto;
            text-align: left;
            background:white;
            box-sizing: border-box;
            padding: 0.36rem;
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            margin-bottom: 0.32rem;
            span{
                font-size: 0.28rem;
                font-weight: 500;
                color:rgb(7, 17, 27);
                height: 0.28rem;
                line-height: 0.28rem;
            }
            p{
                margin-top: 0.12rem;
                padding: 0 0.16rem;
                font-size: 0.24rem;
                font-weight: 200;
                color: rgb(77,85,93);
                line-height: 0.48rem;
            }
        }
        .food-comment{
            width:100%;
            height: auto;
            text-align: left;
            background:white;
            box-sizing: border-box;
            padding: 0.36rem;
            padding-bottom: 0;
            border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            span{
                font-size: 0.28rem;
                font-weight: 500;
                color:rgb(7, 17, 27);
                height: 0.28rem;
                line-height: 0.28rem;
            }
        }
        .comment-details{
            border-top: 0.02rem solid rgba(7, 17, 27,0.1);
            width: 100%;
            height: auto;
            box-sizing: border-box;
            padding: 0 0.36rem;
            background:white;
            .everyone{
                width: 100%;
                height:auto;
                padding: 0.32rem 0 ;
                border-bottom: 0.02rem solid rgba(7, 17, 27,0.1);
                .ereryone-info{
                    display: flex;
                    justify-content: space-between;
                    width: 100%;
                    height: 0.24em;
                    color: rgb(147,153,159);
                    margin-bottom: 0.24rem;
                    .info-left{
                        height: 0.24em;
                        font-size: 0.2rem;
                        line-height: 0.24rem;
                    }
                    .info-right{
                        height: 0.24em;
                        font-size: 0.2rem;
                        line-height: 0.24rem;
                        img{
                            width: 0.24rem;
                            height: 0.24rem;
                            margin-left: 0.12rem;
                            border-radius: 100%;
                            vertical-align: bottom;
                        }
                    }
                }
                .everoyne-comment{
                    text-align: left;
                    height: 0.48rem;
                   i{
                       font-size: 0.24rem;
                       line-height: 0.48rem;
                       &.icon-damuzhion{
                           color: rgb(0,160,220);
                       }
                       &.icon-down{
                           color: rgb(147,153,159);
                       }
                   }
                   span{
                       font-size: 0.24rem;
                       color:rgb(7, 17, 27);
                       line-height: 0.32rem;
                   }
                }
            }
        }

    }
</style>




