<template>
  <div class="foot">
      <div class="buy"  @click="shopShow">
          <i class="iconfont icon-gouwuche"></i>
      </div>
      <div class="total-price">
          {{totalPrice}}
      </div>
      <div class="send-price">
          {{seller.deliveryPrice}}
      </div>
      <div class="min-price"> 
        {{pay}}
      </div>
  </div>
</template>
<script>
    import { mapGetters } from 'vuex';
    export default{
        props:['goods'],
        computed:{
            ...mapGetters([
                'seller'
            ]),
            // count
			selcetFoods(){
				let  foods = [];
				this.goods.forEach( good => {
					good.foods.forEach( food => {
						if( food.count > 0 ){
							foods.push( food );
						}
					})
				});
				return foods;
			},
			// 
			totalCount(){
				let count = 0 ;
				this.selcetFoods.forEach( food => {
					count += food.count;
				});
				return count;
			},
			//
			totalPrice(){
				let price = 0;
				this.selcetFoods.forEach( food => {
					price += food.count * food.price;
				});
				return price;
			},
			//
			pay(){

				if( this.totalPrice === 0 ){
					return ` ${this.seller.minPrice} `;
				}else if ( this.totalPrice < this.seller.minPrice ) {
					return ` ${this.seller.minPrice - this.totalPrice } `
				}else{
					return "";
				}
			}
        },
        methods:{
            shopShow(){
                this.$store.dispatch('showShop');
            }
        }
    }
</script>
<style lang="less" scoped>
    .foot{
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 1rem;
        background:#141d27;
        box-sizing: border-box;
        padding-left: 0.36rem;
        display: flex;
        .buy{
            width: 1.12rem;
            height: 1.12rem;
            border-radius: 100%;
            box-sizing: border-box; 
            border:0.12rem solid #141d27;
            position: absolute;
            bottom:0.16rem;
            background-color:#2b343c;
            display: table;
            i{
                font-size: 0.48rem;
                display: table-cell;
                vertical-align: middle;
                color: rgba(255,255,255,0.4);
                line-height:0.48rem;
            }
       }
       .total-price{
           width: 0.94rem;
           height: 0.52rem;
           line-height: 0.52rem;
           font-size: 0.32rem;
           font-weight: 700;
           color: rgba(255,255,255,0.4);
           margin-top: 0.24rem;
           margin-bottom: 0.24rem;
           margin-left: 1.12rem;
           border-right: 0.02rem solid rgba(255,255,255,0.1);
       }
       .send-price{
           width: 3.1rem;
           height: 1rem;
           line-height: 1rem;
           box-sizing: border-box;
           padding: 0 0.24rem;
           text-align: left;
           font-size: 0.32rem;
           color: rgba(255,255,255,0.4);
           font-weight: 400;
       }
       .min-price{
           width: 2.1rem;
           height: 1rem;
           background: #2b333b;
           box-sizing: border-box;
           padding: 0 0.16rem;
           font-size: 0.24rem;
           color:rgba(255,255,255,0.4);
           font-weight: 700;
           line-height: 1rem;
       }
    }
</style>


